(function () {
    "use strict";
    var cookies = require('cookies');
    var URI = require('urijs');
    var _ = require('lodash');
    require("experiment");
    var localeKeyInCookie = require('docs.config').localeKeyInCookie;
    var allLocaleList = require('docs.config').localeList;

    var currentLang;
    var langList = {
        "de-de": "Deutsch",
        "en-us": "English",
        "es-es": "Español",
        "fr-fr": "Français",
        "it-it": "Italiano",
        "ja-jp": "日本語",
        "ko-kr": "한국어",
        "pt-br": "Português (Brasil)",
        "ru-ru": "Pусский",
        "zh-cn": "中文 (简体)",
        "zh-tw": "中文 (繁體)"
    };

    var fillLangList = function(columns) {
        var keys = _.sortBy(_.keys(langList));
        var langCount = keys.length;
        var currentPickerColumn = columns;
        var chunkSize = Math.ceil(langCount / currentPickerColumn);
        _.each(_.chunk(keys, chunkSize), function (chunk, i) {
            var ul = $(".lang-list ." + (columns === 3? "three" : "two") + "-column-" + (i+1)  + " ul");
            _.each(chunk, function (item) {
                var alink = $("<a>").text(langList[item]).attr('data-id', item);
                if (currentLang === item) {
                    alink.addClass("active");
                }
                ul.append($("<li>").addClass("lang-item").append(alink));
            });
        });
    };

    var getNeutralUrl = function () {
        var browserUrl = URI(window.location.href.toLowerCase());
        var locale = browserUrl.segment()[0];
        if (allLocaleList.indexOf(locale) >= 0) {
            browserUrl.segment(0, "");
        }

        // BUG #563687:
        // The following code will always let the page scroll to the top. Although it is not the behavior specified
        // in the BUG ["remain in the same place"], it is expected in reality ["locale picker is always at the top"].
        // In order not to introduce the complexity of preserving and retrieving page's scroll-top information, let's
        // keep this as a quick fix.
        browserUrl.fragment("");

        return browserUrl.toString();
    };

    var buildLangList = function () {
        fillLangList(3);
        fillLangList(2);
        $('.lang-item > a').click(function () {
            var selected = $(this).data('id');
            if (selected && langList[selected]) {
                $('.lang-item > a.active').removeClass('active');
                $('.lang-item > a[data-id="' + _.escape(selected) + '\"]').addClass('active');
                // set max expiration date
                cookies.setCookie(localeKeyInCookie, selected, 'Fri, 31 Dec 9999 23:59:59 GMT');
                window.location.href = getNeutralUrl();
            }
        });
    };

    var getLanguageFromUrl = function () {
        var browserUrl = URI(window.location.href.toLowerCase());
        var locale = browserUrl.segment()[0];
        if (allLocaleList.indexOf(locale) >= 0) {
            // a valid locale
            return locale;
        }
        return undefined;
    };

    var inspectCurrentLang = function() {
        currentLang = getLanguageFromUrl() || cookies.getCookie(localeKeyInCookie) || 'en-us';
        if (!langList[currentLang]) {
            currentLang = 'en-us';
        }
        $('.selected-lang').html(langList[currentLang]);

    };
    $(document).ready(function() {
        window.Experiment.IsEnabledAsync('locale-picker-phase2').then(function(enabled){
            if (enabled) {
                langList = _.extend(langList, {
                    "cs-cz": "Čeština",
                    "hu-hu": "Magyar",
                    "nl-nl": "Nederlands",
                    "pl-pl": "Polski",
                    "pt-pt": "Português (Portugal)",
                    "sv-se": "Svenska",
                    "tr-tr": "Türkçe"
                });
            }
            inspectCurrentLang();
            buildLangList();
            var showLangPickerDelayed = null, hideLangPickerDelayed = null;
            var delayTime = 200;
            $(".lang-hover").on({
                mouseenter: function () {
                    showLangPickerDelayed = setTimeout(function () {
                        $(".lang-picker").addClass('active');
                        $(".lang-list").show();
                    }, delayTime);
                    if (hideLangPickerDelayed) {
                        clearTimeout(hideLangPickerDelayed);
                    }
                }, mouseleave: function () {
                    hideLangPickerDelayed = setTimeout(function () {
                        $(".lang-picker").removeClass('active');
                        $(".lang-list").hide();
                    }, delayTime);
                    if (showLangPickerDelayed) {
                        clearTimeout(showLangPickerDelayed);
                    }
                }
            });
        });

        // enable locale pick by ab testing flag:
        window.Experiment.IsEnabledAsync('locale-picker').then(function(enabled){
            if (enabled) {
                $('.lang-picker').show();
            }
        });
    });
    module.exports.getLanguageFromUrl = getLanguageFromUrl;
})();


